
.mint-toast {
    position: fixed;
    max-width: 80%;
    border-radius: 5px;
    background: rgba(0, 0, 0, 0.7);
    color: #fff;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    text-align: center;
    z-index: 1000;
    -webkit-transition: opacity .3s linear;
    transition: opacity .3s linear
}
.mint-toast.is-placebottom {
    bottom: 50px;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0)
}
.mint-toast.is-placemiddle {
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%)
}
.mint-toast.is-placetop {
    top: 50px;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0)
}
.mint-toast-icon {
    display: block;
    text-align: center;
    font-size: 56px
}
.mint-toast-text {
    font-size: 14px;
    display: block;
    text-align: center
}
.mint-toast-pop-enter, .mint-toast-pop-leave-active {
    opacity: 0
}

html,
.container,
.imgbox {
  width: 100%;
}
body {
  margin: 0;
}
.imgbox {
  position: relative;
}
.imgbox img {
  width: 100%;
}
.btn {
  position: fixed;
  width: 4rem;
  bottom: 1rem;
  left: 50%;
  margin-left: -2rem;
  text-align: center;
}
.btn > img {
  width: 100%;
}
.number {
  position: absolute;
  width: 100%;
  height: 0.82rem;
  top: 4.64rem;
  font-size: 0.24rem;
  text-align: center;
  line-height: 0.82rem;
}
.number > span {
  font-size: 0.4rem;
  font-weight: bold;
}